    window.removeEventListener('scroll', this.handleScroll, false)
  },
  computed: {
    swiper() { return this.$refs.mySwiper.$swiper },
  },
  methods: {
    scrollEvent(id) {
      this.tempStop = true
      this.$nextTick(() => {
        this.$refs[id].scrollIntoView({ behavior: 'smooth' })
      })
    },
    handleScroll() {
      if (this.tempStop) {
        setTimeout(() => {
          this.tempStop = false
          this.handleScroll()
        }, 1000)
      } else {
        this.$nextTick(() => {
          this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop || 0
          for (let i = 0; i < this.sidebarList.length; i++) {
            if (this.scrollTop + 200 > this.$refs[this.sidebarList[this.sidebarList.length - i - 1].scroll].offsetTop) {
              this.currentPosition = this.sidebarList[this.sidebarList.length - i - 1].scroll
              return
            }
          }
        })
      }

    }
